<template>
  <li class="song" @click="setCurrentSongId(songItem.id)">
    <div class="left" :class="index < 3  && isShow ? 'hott' : 'nuo' " v-if="index !== undefined">{{index > 8? index+ 1:"0" + (index + 1)}}</div>
    <dl class="center">
      <dt>{{songItem.name}}</dt>
      <dd v-if="songItem.song">
        <i class="sghot" v-if="(songItem.song.hMusic) && isShowQuality"></i>
        {{songItem.song.artists[0].name}}-{{songItem.song.album.name}}
      </dd>
      <dd v-else-if="songItem.h">
        <i class="sghot" v-if="(songItem.h) && isShowQuality"></i>
        {{songItem.ar[0].name}}-{{songItem.al.name}}
      </dd>
       <!--  -->
      <dd v-if="songItem.artists">{{songItem.artists[0].name}}-{{songItem.album.name}}</dd>
    </dl>
    
    <div class="right">
      <span></span>
    </div>
    <!-- <div>{{songItem}}</div> -->
  </li>
</template>

<script>
export default {
  props: ["songItem", "isShowQuality", "index","isShow"],
  // data() {
  //   return {
  //     message:""
  //   }
  // },
  // computed: {
  //   message:function(){
  //     return this.message = index >9 ? index + 1:"0" +( index+1);
  //   }
  // },
  methods: {
    setCurrentSongId(id) {
      this.$emit("translate-currentid", id);
      // window.console.log(id);
    }
  }
};
</script>

<style lang="less">
.hott{
      color: #df3436;
    }
li.song .right {
  width: 50px;
  height: 49px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  & > span {
    display: inline-block;
    width: 22px;
    height: 22px;
    background: url(../assets/index_icon.png) no-repeat;
    background-position: -24px 0;
    background-size: 166px 97px;
  }
}
.sghot {
  display: inline-block;
  width: 12px;
  height: 8px;
  margin-right: 4px;
  background: url(../assets/index_icon.png) no-repeat;
  background-size: 166px 97px;
}
// .red{
//   color: red;
//   border: 1px solid red;
//   // display: inline-block;
//   // padding: 0 2px;
//   font-size: 4px;
// }
li.song {
  display: flex;
  dl.center {
    flex: 1;
    overflow: hidden;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);

    dt {
      line-height: 24.6px;
      font-size: 17px;
      color: #333;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      margin-top: 2px;
    }
    dd {
      line-height: 17.4px;
