<template>
  <div class="search">
    <label class="search-input">
      <!-- .trim 修饰符会自动过滤掉输入的首尾空格 -->
      <input type="text" v-model.trim="inputValue" placeholder="搜索歌手、歌曲、专辑"
      @keyup.enter="getSearchMultimatch(); getSearchSongs()" />
      <button v-if="inputValue" v-on:click="inputValue=null " class="reset"></button>
    </label>

    <template v-if="!inputValue">
      <div class="hot">
        <h5>热门搜索</h5>
        <ul class="keywords">
          <li
            ref="refname"
            v-on:click="handle(index)"
            v-for="(item, index) in hotSearchKeyWords"
            :key="index"
          >{{item.first}}</li>
        </ul>
      </div>
      <div class="history">搜索历史</div>
    </template>

    <!-- 啧啧啧！！！ -->
    <template v-if="inputValue && !searchSongs">
      <ul class="lis">
        <li>搜索“{{inputValue}}”</li>
        <li v-for="(item, index) in searchSuggests"
        ref="refnames"
        v-on:click="handles(index)"
         :key="index">{{item.keyword}}</li>
      </ul>
    </template>

    <div class="multimatch" v-if="searchMultimatch">
      <template v-if="searchMultimatch.artist">
        <div class="artist" v-for="(item) in searchMultimatch.artist" :key="item.id" @click="$router.push('/artist?id='+item.id)">
          <img :src="item.picUrl" alt />
          <h5>歌手: {{item.name}}</h5>
          <span>></span>
        </div>
      </template>
      
      <template v-if="searchMultimatch.mv">
        <div class="mv" v-for="(item) in searchMultimatch.mv" :key="item.id">
          <img :src="item.cover" alt />
          <h5>
            <p>MV: {{item.name}}</p>
            <p>{{item.artistName}}</p>
          </h5>
          <span>></span>
        </div>
      </template>
    </div>

    <ul v-if="searchSongs && inputValue">
      <SongListItem
        v-for="(item, index) in searchSongs"
        :key="item.id"
        :songItem="item"
        :index="index"
        @translate-currentid="$emit('translate-currentid', $event)"
      ></SongListItem>
    </ul>

    <Loading v-if="loading"></Loading>
  </div>
</template>
    
<script>
import Loading from "@/components/Loading.vue";
import SongListItem from "@/components/SongListItem.vue";
export default {
  name: "HomeSearch",
  components: {
    Loading,
    SongListItem
  },
  data() {
    return {
      hotSearchKeyWords: [],
      inputValue: null,
      searchSuggests: [],
      loading: false,
      searchMultimatch: null,
      searchSongs: []
    };
  },
  methods: {
    handle(index) {
      // window.console.log(this.$refs.refname[index].innerText);
      this.inputValue = this.$refs.refname[index].innerText;
    },
    handles(index) {
      // window.console.log(this.$refs.refname[index].innerText);
      this.inputValue = this.$refs.refnames[index].innerText;
    },
    getHotSearch() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search/hot")
        .then(response => {
          {
            ({ hots: this.hotSearchKeyWords } = response.data.result);
          }
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getSearchSuggest() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search/suggest", {
          params: {
            keywords: this.inputValue,
            type: "mobile"
          }
        })
        .then(response => {
          this.loading = false;
          ({ allMatch: this.searchSuggests } = response.data.result);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getSearchMultimatch() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search/multimatch", {
          params: {
            keywords: this.inputValue
          }
        })
        .then(response => {
          ({ result: this.searchMultimatch } = response.data);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getSearchSongs() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search", {
          params: {
            keywords: this.inputValue,
            limit: 5,
            offset: 0
          }
        })
        .then(response => {
          ({ songs: this.searchSongs } = response.data.result);
        })
        .finally(() => {
          this.loading = false;
        });
    }
  },
  created() {
    this.getHotSearch();
  },
  watch: {
    inputValue(newValue) {
      if(newValue){
        this.getSearchSuggest();
        this.searchSongs = null;
      }
      // else if(newValue){
      //   this.searchSongs = null;
      // }
      
    }
  }
};
</script>

<style lang="less">
body {
  background-color: #fcfcfd;
}
  .multimatch {
    .artist {
      display: flex;
      line-height: 50px;
      img {
        width: 50px;
        height: 50px;
      }
      h5 {
        flex: 1;
      }
      span {
        width: 20px;
      }
    }

    .mv {
      display: flex;
      line-height: 50px;
      img {
        width: 90px;
        height: 50px;
      }
      h5 {
        flex: 1;
        p {
          &:first-child {
            line-height: 30px;
          }
          &:last-child {
            line-height: 20px;
          }
        }
      }
      span {
        width: 20px;
      }
    }
  }
.hot > h5 {
  padding-top: 20px;
}
.lis {
  & > li:nth-child(1) {
    height: 50px;
    margin-left: 10px;
    padding-right: 10px;
    font-size: 15px;
    line-height: 50px;
    color: #507daf;
  }
  & > li:not(:first-child) {
    margin-left: 25px;
    padding-right: 10px;
    font-size: 15px;
    line-height: 45px;
    color: #333;
    border-bottom: 0.5px solid gray;
    position: relative;
    &::after {
      content: "";
      top: 50%;
      margin-top: -7.5px;
      left: -30px;
      width: 15px;
      height: 15px;
      position: absolute;
      background: url(data:image/svg+xml;base64,);
    }
  }
}
.loading {
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -50px;
}
.search {
  padding: 0 15px;
  .search-input {
    position: relative;
    input {
      margin-top: 20px;
      background: #ebecec;
      width: 100%;
      height: 30px;
      outline: none;
      border: 1px solid gray;
      border-radius: 15px;
      padding: 0 25px;
    }
    &::before,
    .reset {
      content: "";
      width: 14px;
      height: 14px;
      position: absolute;
      top: 6px;
      left: 10px;
      background: url(data:image/svg+xml;base64,);
    }
    .reset {
      left: auto;
      right: 6px;
      border: none;
      outline: none;
      background: url(data:image/svg+xml;base64,);
    }
  }
  .keywords {
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
    & > li {
      border: 1px solid gray;
      border-radius: 15px;
      height: 32px;
      margin-right: 8px;
      margin-bottom: 8px;
      padding: 0 14px;
      font-size: 14px;
      line-height: 32px;
      color: #333;
    }
  }
}
</style>