<template>
  <div class="player" :class="{paused: isPaused}" v-if="currentSongDetail">
    <audio :src="currentSongUrl" controls autoplay></audio>

    <div class="player-bar" @click="isShowFullscreenPlayer=true">
      <img :src="currentSongDetail.al.picUrl" alt />
      <h3>{{currentSongDetail.name}}</h3>
      <span class="progress" @click.stop="pausedAudio">
        <canvas width="36" height="36"></canvas>
      </span>
    </div>
    <template v-if="isShowFullscreenPlayer">
      <div
        class="player-fullscreen"
        :style="{'background-image': `url(${currentSongDetail.al.picUrl})`}"
      >
        <mark @click="isShowFullscreenPlayer=false">下</mark>
      </div>
    </template>
  </div>
</template>


<script>
export default {
  props: ["currentSongId"],
  data() {
    return {
      currentSongDetail: null,
      isPaused: false,
      isShowFullscreenPlayer: false,
      currentTime: 0,
      animateId: null
    };
  },
  computed: {
    currentSongUrl() {
      return (
        "https://music.163.com/song/media/outer/url?id=" +
        this.currentSongId +
        ".mp3"
      );
    }
  },
  methods: {
    getSongDetail() {
      this.axios
        .get("http://music.kele8.cn/song/detail", {
          params: {
            ids: this.currentSongId
          }
        })
        .then(response => {
          window.console.log(response);
          this.currentSongDetail = response.data.songs[0];
        });
    },
    pausedAudio() {
      let audio = this.$el.querySelector("audio");

      if (this.isPaused) {
        audio.play();
      } else {
        audio.pause();
      }
      this.isPaused = audio.paused;
      // paused 属性返回音频是否已暂停。
    },
    drawCircleProgress() {
      let audio = this.$el.querySelector("audio");

      let canvas = this.$el.querySelector("canvas");
      let ctx = canvas.getContext("2d");

      let id = window.setInterval(() => {
        this.currentTime = audio.currentTime * 1000;

        ctx.clearRect(0, 0, 36, 36);

        ctx.beginPath();
        ctx.moveTo(12, 9);
        ctx.lineTo(27, 18);
        ctx.lineTo(12, 27);
        ctx.closePath();
        ctx.fillStyle = 'red';
        ctx.fill();
        ctx.stroke();

        ctx.beginPath();
        ctx.arc(18, 18, 17, 0, 2 * Math.PI);
        ctx.closePath();
        ctx.strokeStyle = "#ccc";
        ctx.stroke();

        ctx.beginPath();
        ctx.arc(
          18,
          18,
          17,
          -0.5 * Math.PI,
          2 * Math.PI * (this.currentTime / this.currentSongDetail.dt) -
            0.5 * Math.PI
        );
        ctx.strokeStyle = "red";
        ctx.stroke();
      }, 1000);
      return id;
    }
  },
  created() {
    // 第一次进来就拿数据
    this.getSongDetail();
  },
  watch: {
    currentSongId: function() {
      // 每次id变化也拿数据
      this.getSongDetail();
    },
    isPaused() {
      if (this.isPaused) {
        window.clearInterval(this.animateId);
      } else {
        window.clearInterval(this.animateId);
        this.animateId = this.drawCircleProgress();
      }
    },
    currentSongDetail() {
      // 第一次进入 后面每次更新
      this.$nextTick(function() {
        window.clearInterval(this.animateId);
        this.animateId = this.drawCircleProgress();
      });
    }
  },
  updated() {}
};
</script>

<style lang="less">
.player {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  //   height: 60px;
  audio {
    height: 60px;
  }
  .player-bar {
    display: flex;
    border-top: 1px solid lightgray;
    background: rgba(255, 255, 255, 0.95);
    padding: 7px 10px;
    line-height: 36px;
    img {
      width: 36px;
      height: 36px;
      border-radius: 50%;
      animation: myrolling 5s linear infinite;
    }
    @keyframes myrolling {
      from {
        transform: rotate(0);
      }
      to {
        transform: rotate(360deg);
      }
    }
    h3 {
      flex: 1;
      margin: 0 10px;
    }
    .progress {
      width: 36px;
      height: 36px;
      //   background: yellow;

      text-align: center;
    }
  }
  &.paused {
    .player-bar {
      img {
        animation-play-state: paused;
      }
    }
  }

  .player-fullscreen {
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-position: center;
    // filter: blur(10px);
  }
}
</style>