<template>
  <div>
    <div class="hot">
      <div class="nav">
        <div class="logo"></div>
        <div class="date">更新日期：01月02日</div>
      </div>
    </div>

    <ul class="songhot">
      <template v-if="playhot">
        <SongListItem v-for="(item, index) in playhot" 
        :key="index"
        :song-item="item"
        :index ="index"
        :is-show-quality="true"
        :is-show="true"
        @translate-currentid="$emit('translate-currentid',$event)"
        />
      </template>
    </ul>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
export default {
  components: {
    SongListItem,
  },
  data: function() {
    return {
      playhot: [],
    };
  },
  // computed: {
  //   playhot:function(){
  //     return this.playhot.slice(0,19)
  //   }
  // },
  methods: {
    getPlayHot() {
      this.axios.get("http://music.kele8.cn/top/list?idx=1").then(response => {
        // ({ result: this.playhot } = response);
        this.playhot = response.data.playlist.tracks.slice(0,20);
        // this.playhot = response.data.playlist.tracks;
        // window.console.log(response)
      });
    }
  },
  created() {
    // 组件创建完成 拿数据
    this.getPlayHot();
  }
};
</script>

<style lang="less" scoped>
.hot {
  width: 100%;
  height: 160px;
  background: url(../assets/hot.jpg) no-repeat;
  background-size: 100%;
  display: flex;
  align-items: center;
  padding-left: 20px;
  .nav {
    // flex-direction: column;
    .logo {
      width: 142px;
      height: 67px;
      background: url(../assets/index_icon.png) no-repeat;
      background-size: 166px 97px;
      background-position: -23px -30px;
    }
    .date {
      margin-top: 8px;
      color: hsla(0, 0%, 100%, 0.8);
      font-size: 12px;
    }
  }
}
</style>